import { Component, OnInit } from '@angular/core';
import { AuthService } from 'src/app/services/auth/auth.service';
import { ApiService } from 'src/app/services/api/api.service';
import { RouterModule, Router } from '@angular/router';

@Component({
  selector: 'app-signin',
  templateUrl: './signin.component.html',
  styleUrls: ['./signin.component.css']
})
export class SigninComponent implements OnInit {

  constructor(private auth:AuthService, private api:ApiService, private router:Router) { }

  ngOnInit() {
  }



  user={
    email:"asmarakhan@gmail.com",
    password: "asmara123"
  }


  submit(data){
    console.log(data);
    this.auth.login(data.email, data.password).then(resp=>{
      this.api.getCustomer(resp.user.uid).subscribe(res=>{
        console.log(res);
        if(res){
          console.log(`Customer Logged In`);
          this.auth.setToken(resp.user.uid);
          this.auth.setProfile(res);
          this.auth.loggedIn =true;
          this.router.navigate(['/dashboard/profile'])
        }else{
          console.log(`no data found in db..`);
        }

      }, error=> console.log(error))
    },err=>{
      console.log(err);
    })
  }
}
