import { Injectable } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import { AngularFireStorage } from '@angular/fire/storage';

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  constructor(private afs:AngularFirestore) { }



  /* :::::::::::: CUSTOMER ::::::::::::::: */


  // ~ CREATE
  createCustomer(uid, data){
    return this.afs.doc('customers/'+uid).set(data);
  }
// ~ GET
getCustomer(uid){
  return this.afs.doc('customers/'+uid).valueChanges();
}

// ~ DELETE
deleteCustomer(uid){
  return this.afs.doc('customers/'+uid).delete();
}

// ~ UPDATE
updateCustomer(uid, data){
  return this.afs.doc('customers/'+uid).update(data);
}





  /* :::::::::::: CATEGORIES + SUBCATEGORIES::::::::::::::: */

// ~ GET
getCategories(){
  return this.afs.collection('sections').snapshotChanges();
}
getSubCategories(){
  return this.afs.collection('categories').snapshotChanges();
}
getSectionCategories(sectionId){
  return this.afs.collection('categories', ref=> ref.where('sectionId', '==',sectionId)).snapshotChanges();
}

  /* :::::::::::: ADS ::::::::::::::: */
  // ~ CREATE
  createAd( data){
    return this.afs.collection('ads').add(data);
  }
// ~ GET
getAd(uid){
  return this.afs.doc('ads/'+uid).valueChanges();
}
getAds(){
  return this.afs.collection('ads').snapshotChanges();
}
// ~ GET Customer Ads
getCustomerAds(customerId){
  return this.afs.collection('ads',ref => ref.where('customerId','==',customerId)).snapshotChanges();
}

// ~ GET Category Ads
getCategoryAds(categoryId){
  return this.afs.collection('ads',ref => ref.where('sectionId','==',categoryId)).snapshotChanges();
}
// ~ GET Sub-Category Ads
getSubCategoryAds(subCategoryId){
  return this.afs.collection('ads',ref => ref.where('categoryId','==',subCategoryId)).snapshotChanges();
}
// ~ GET User Favorite Ads
getCustomerFavoriteAds(customerId){
  return this.afs.collection('favorites',ref => ref.where('customerId','==',customerId)).snapshotChanges();
}
// ~ DELETE
deleteAd(uid){
  return this.afs.doc('ads/'+uid).delete();
}

// ~ UPDATE
updateAd(uid, data){
  return this.afs.doc('ads/'+uid).update(data);
}




getDate(timestamp){
  return timestamp.toDate().toDateString();
}










}
