import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { ApiService } from 'src/app/services/api/api.service';
import { map } from 'rxjs/operators/map';
import { finalize } from 'rxjs/operators';
import { AngularFireStorage } from '@angular/fire/storage';

@Component({
  selector: 'app-post-ad',
  templateUrl: './post-ad.component.html',
  styleUrls: ['./post-ad.component.css']
})
export class PostAdComponent implements OnInit {

  constructor(private api:ApiService,private storage:AngularFireStorage) { }

  ngOnInit() {
    this.getCustomer();
    this.getCategories();
  }

  photos=[]


  ad={
    title:'',
    image:'',
    condition:'',
    price:'',
    sellerType:'',
    description:'',
    customerName:'',
    customerId:'',
    date:null,
    featured:null,
    negotiable:false,
    phone:null,
    sectionName:'',
    sectionId:'',
    verified:false,
    country:'',
    city:'',
    address:'',
    email:'',
    photos:[]

  }
  data;
  sections;
  categories;


  category;
  section;

  getCustomer(){
    let id = localStorage.getItem('uid');
    console.log(id);
    this.api.getCustomer(id).subscribe(resp=>{
      this.data =resp;
      this.ad.customerName = this.data.name;
      this.ad.customerId = id;
      this.ad.email = this.data.email;
      this.ad.phone = this.data.phone;

    })
  }




  getCategories(){
    this.api.getCategories()
    .pipe(map( docArray => {
      return docArray.map( doc => {
        let data = doc.payload.doc.data();
        let id = doc.payload.doc.id;
        return(
          {
          id,...data }
        // doc
      );
      });
    }))
    .subscribe(res=>{
      this.sections = res;
      console.log(this.sections);
    })
  }
  getSubCategories(sectionId){
    this.api.getSectionCategories(sectionId)
    .pipe(map( docArray => {
      return docArray.map( doc => {
        let data = doc.payload.doc.data();
        let id = doc.payload.doc.id;
        return(
          {
          id,...data }
        // doc
      );
      });
    }))
    .subscribe(res=>{
      this.categories =res;
      console.log(this.categories);
    })
  }





  postAd(){
    this.ad.date = new Date();
    //uploading files
let x = new Promise( (resolve,reject)=>{
  let items=0;
  this.photos.forEach((file, index)=>{
    const filePath = this.ad.customerId +'/ads/'+file.name;
  const fileRef = this.storage.ref(filePath);
  const task = this.storage.upload(filePath, file);
  task.snapshotChanges().pipe(
    finalize(() => {
      fileRef.getDownloadURL().subscribe(resp=>{
        console.log(resp);
        this.ad.photos.push(resp);
      });
      items++;
      console.log(items);
      if(items === this.photos.length){
        console.log(this.ad.photos);
        resolve();
      } }) )
.subscribe()  })
}).then(dox=>{
  this.ad.image = this.ad.photos[0];
  let data ={...this.ad, ...this.category, ...this.section};
  console.log(data);
  this.api.createAd(data).then(res=>{
    console.log(`Ad has been created bitch!`)
  })
})




  }



  handleSection(e){
    this.categories=null;
let data = JSON.parse(e.target.value);
console.log(data);
this.section = data;
//update subcategories
this.getSubCategories(data.sectionId);

  }
  handleSubCategory(e){
let data = JSON.parse(e.target.value);
console.log(data);
this.category = data;

  }

img:any;
  fileChange(event,num) {
    let fileList: FileList = event.target.files;
    if(fileList.length > 0) {
      let file: File = fileList[0];
      this.img = document.querySelector("#preview"+num+" img");

      this.img.file = file;
      this.photos.push(file);
      console.log(this.photos);

      var reader = new FileReader();
      reader.onload = (function(aImg) { return function(e) {
         aImg.src = e.target.result;
        };
      })(this.img);
      reader.readAsDataURL(file);
    }
  }


}
